@import '../../../style/color';

.requestContainer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;

  font-size: 1.6rem;
  width: calc(100% - 80px);
  height: 100vh;
  width: 58%;
}

.request {
  display: flex;
  align-items: start;
  justify-content: space-between;
  padding: 6% 2% 0;
  width: 100%;
  height: 100%;
}

.requestLeft {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  li {
    margin: 10px 0;
    img {
      width: 100px;
      margin-top: 20px;
    }
    p {
      text-align: center;
      font-size: 1.4rem;
      line-height: 20px;
    }
  }
}

.title {
  font-size: 3.2rem;
  color: #333;
  font-weight: bold;
  font-family: 'Laila', sans-serif;
  padding: 0 10px;
  border-bottom: 3px solid #888;
}

.request_text {
  text-indent: 10px;
  color: #666;
  font-weight: bold;
  font-size: 1.4rem;
  margin: 10px 0;
}

.required_mark {
  display: block;
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  left: 0;
  top: 0;
  background-color: $main-color;
}

.input_list {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  width: 100%;
  height: 100%;
  padding: 40px 20px 30px;

  ul {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 0 0 30px;
  }
  li {
    width: 45%;
    label {
      display: block;
      line-height: normal;
      cursor: pointer;
      font-size: 1.4rem;
      font-weight: bold;
    }
  }
}

.request_input {
  border-bottom: 2px solid #a7a7a7;
  color: #b6b6b6;
  font-size: 1.4rem;
  font-weight: bold;
  width: 100%;
  height: 35px;
  padding: 0 10px;
}
.request_input:focus {
  outline: none;
}

.requestRight {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.upload {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  li {
    padding: 0;
    margin: 20px 0;
    width: 100%;

    &:first-child {
      display: flex;
    }
    & > li:last-child {
      display: flex;
    }
    & > li {
      display: flex;
    }
    & > li:first-child {
      label {
        width: 120px;
        padding-left: 10px;
      }
    }

    textarea {
      padding: 10px 10px;
      &::placeholder {
        color: #b6b6b6;
        font-weight: normal;
      }
    }
  }
}
.project_nameLabel {
  width: 120px;
  padding-left: 10px;
}
.project_name {
  border-top: none;
  border-left: none;
  border-right: none;
  color: #b6b6b6;
  font-size: 1.4rem;
  font-weight: bold;
  border-bottom: 2px solid #a7a7a7;
  width: 100%;
  height: 40px;
  padding: 0 10px;
}

.project_detailLabel {
  display: block;
  font-size: 1.4rem;
  cursor: pointer;
  width: 100%;
}
.text_area:focus {
  border-bottom: 2px solid $main-color;
}
.text_area {
  resize: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #a7a7a7;
  outline: none;
  color: #b6b6b6;
  font-size: 1.4rem;
  font-weight: bold;
  font-family: inherit;
  width: 100%;
}
.project_file {
  border-top: none;
  border-left: none;
  border-right: none;
  color: #b6b6b6;
  font-size: 1.4rem;
  font-weight: bold;
  border-bottom: 2px solid #a7a7a7;
  width: 100%;
  height: 40px;
  padding: 0 10px;
}

.request_policyIn {
  display: flex;
  .checkBox {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    left: 0;
    top: 2px;
    background-color: #eaeaea;
    border-radius: 2px;
  }
  label {
    cursor: pointer;
    padding-left: 30px;
    color: #333;
    a {
      font-weight: bold;
      color: #333;
    }
  }
}

.request_policy {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 90%;
  height: 100px;
  text-align: center;
}

.policyBtn {
  margin-top: 20px;
  border: 2px solid $main-color;
  border-radius: 10px;
  padding: 10px 20px;
  transition: all 0.3s;
  cursor: pointer;
  color: $main-color;
  background: transparent;
  font-weight: bold;
  font-size: 1.8rem;
}

.file_input_div {
  position: absolute;
  width: 30px;
  height: 30px;
  overflow: hidden;
  right: 20px;
  img {
    position: relative;
    right: 0;
    bottom: 0;
    font-size: 2.4rem;
    color: #dc6868;
    cursor: pointer;
    width: 25px;
  }
}

.fileInput {
  display: flex;
}

@media (min-width: 1025px) and (max-width: 1400px) {
  .request {
    display: block;
    padding: 3% 2%;
    width: 100%;
    height: 110%;
  }

  .requestLeft {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    right: 20px;
    top: 40px;
  }

  .requestRight {
    height: 100%;
    width: 100%;
    top: 40px;
  }

  .requestContainer {
    left: 40px;
  }
}

@media (max-width: 1024px) {
  .requestContainer {
    width: calc(100% - 80px);
    height: 100%;
    left: -35px;
    top: 30px;
  }

  .request {
    display: block;
    padding: 3% 2%;
    width: 200%;
    height: 110%;
  }

  .requestLeft {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
  }

  .requestRight {
    height: 100%;
    margin: 0 5%;
    padding: 4% 20px 0;
    width: 100%;
  }
}

@media (max-width: 770px) {
  .requestContainer {
    width: 100%;
    height: 100%;
    left: -35px;
    top: 30px;
  }

  .requestLeft {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
  }
  li {
    margin: 10px 0;
    img {
      display: none;
    }
    p {
      text-align: center;
      font-size: 1.4rem;
      line-height: 20px;
    }
  }
  .input_list {
    width: 90%;
    margin: 8% 0;
    justify-content: flex-start;
    padding: 0;
    ul {
      display: block;
      width: 100%;
      margin: 0;
      li {
        margin: 0 0 40px 0;
        width: 100%;
      }
    }
  }
}
