@import './font';
@import './color';

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  line-height: 1.4;
  vertical-align: baseline;
}

html {
  overflow-x: hidden;
  font-size: 10px;
  font-family: 'IBMPlexSansKR-Regular';
  color: #222222;
}

body {
  box-sizing: border-box;
  &::-webkit-scrollbar {
    width: 20px;
  }
}
html {
  overflow-x: hidden;
  font-size: 10px;
  font-family: 'IBMPlexSansKR-Regular';
  color: #222222;
}

body {
  &::-webkit-scrollbar-thumb {
    background-color: $main-color;
    border-radius: 30px;
  }
}
